<template>
  <div>
    <el-form v-loading="loading" ref="form" :model="form" label-width="80px">
      <el-form-item label="IP/">
        <el-input v-model="form.ip" @input="handleFormat" placeholder="/IP eg: 10.12.13.24 IP eg: 10.12.13.24,10.12.13.25 IP eg: 10.12.13.0/24 IP eg: 10.12.13.1-10.12.13.20"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="form.region" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value="cn"></el-option>
          <el-option label="" value="hk"></el-option>
          <el-option label="" value="us"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="form.tags" filterable allow-create multiple placeholder="/">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" v-if="showItem">
        <el-tooltip class="item" effect="dark" content=", DNS, " placement="top-start">
          <el-switch v-model="form.scan_sub_domain"></el-switch>
        </el-tooltip>
        <el-alert :closable="false" :type="form.scan_sub_domain ? 'success' : 'info'" title=", DNS, "/>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'AssetsCreate',
  data () {
    return {
      showItem: false,
      options: [],
      loading: false,
      form: {
        ip: '',
        region: '',
        scan_sub_domain: false,
        tags: []
      }
    }
  },
  created () {

  },
  methods: {
    onSubmit () {
      let that = this
      this.loading = true
      this.$http.post('/add/ip', this.form).then(res => {
        that.loading = false
        if (res.data.state) {
          that.$message.success(', ')
        } else {
          that.$message.error(res.data.msg)
        }
      })
    },
    handleChange () {
      let ips = this.form.ip.split('.')
      if ((ips.length === 2 && ips[1] !== '' && ips[0] !== 'www') || (ips.length === 3 && ips[0] === 'www')) {
        this.showItem = true
        this.form.scan_sub_domain = true
      } else {
        this.showItem = false
        this.form.scan_sub_domain = false
      }
      if (this.form.ip.indexOf(',') !== -1) {
        this.showItem = true
        this.form.scan_sub_domain = false
      }
    },
    handleFormat () {
      let ips = this.form.ip.split('/')
      this.form.ip = ips.length > 2 ? ips[2] : this.form.ip
      this.handleChange()
    }
  }
}
</script>

<style scoped>

</style>



